import { useState } from 'react';
import Image from 'next/image';

export default function CommentsSection() {
  const [comments, setComments] = useState([
    {
      name: 'Matthew Larson',
      date: '8 December 2025',
      text: 'This is super helpful! Iâ€™ve always found GST and superannuation a bit confusing, but your breakdown makes it much clearer. Excited to read more about lodging taxes online!',
      avatar: '/author1.png',
    },
    {
      name: 'Murtaza Ali Haider',
      date: '9 December 2025',
      text: 'Thank you so much! Iâ€™m glad you found the breakdown helpful. Iâ€™ll definitely be sharing more insights on lodging taxes online soon â€” stay tuned!',
      avatar: '/author2.png',
    },
  ]);

  const [newComment, setNewComment] = useState('');

  const handleAddComment = () => {
    if (newComment.trim() !== '') {
      const newEntry = {
        name: 'You',
        date: new Date().toLocaleDateString(),
        text: newComment,
        avatar: '/author2.png',
      };
      setComments([...comments, newEntry]);
      setNewComment('');
    }
  };

  return (
    <div className="max-w-2xl mx-auto p-4 bg-white shadow-md rounded-md">
      <h2 className="text-xl font-semibold mb-4">Add Comments</h2>
      <div className="flex gap-2 mb-4">
        <input
          type="text"
          placeholder="Add a comment...."
          value={newComment}
          onChange={(e) => setNewComment(e.target.value)}
          className="flex-1 p-2 border rounded-md"
        />
        <button
          onClick={handleAddComment}
          className="bg-black text-white py-2 px-4 rounded-lg hover:bg-gray-800 transition"
        >
          âž¤
        </button>
      </div>

      <div className="flex items-center gap-2 mb-4">
        <span className="font-semibold">Share:</span>
        <button className="bg-gray-200 p-2 rounded-full hover:bg-gray-300">ðŸ“²</button>
        <button className="bg-gray-200 p-2 rounded-full hover:bg-gray-300">ðŸ“¸</button>
        <button className="bg-gray-200 p-2 rounded-full hover:bg-gray-300">ðŸ“˜</button>
      </div>

      {comments.map((comment, index) => (
        <div
          key={index}
          className={`p-4 mb-2 rounded-md ${index % 2 === 0 ? 'bg-orange-100' : 'bg-gray-100'}`}
        >
          <div className="flex items-center gap-2 mb-2">
            <Image src={comment.avatar} alt={comment.name} width={40} height={40} className="rounded-full" />
            <div>
              <p className="font-bold">{comment.name}</p>
              <p className="text-sm text-gray-500">{comment.date}</p>
            </div>
          </div>
          <p className="text-gray-700 mb-2">{comment.text}</p>
          <button className="text-sm text-gray-600 hover:text-gray-800">â†ª Reply</button>
        </div>
      ))}
    </div>
  );
}
